import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ServicesComponent } from '../services/services.component';
import { BradingComponent } from '../brading/brading.component';
import { WebsiteComponent } from '../website/website.component';
import { EcommerceComponent } from '../ecommerce/ecommerce.component';
import { GraphicsComponent } from '../graphics/graphics.component';

const routes: Routes = [
  { path: '', component: ServicesComponent },
  { path: 'branding', component: BradingComponent },
  { path: 'website-development', component: WebsiteComponent },
  { path: 'ecommerce-web-development', component: EcommerceComponent },
  { path: 'graphics', component: GraphicsComponent },
  { path: '**', component:  ServicesComponent},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ServiceRoutingModule { }
