	onMouseEnter(index: number): void {
		let projectname = this.allprojects[index];
		let images = projectname.project_images;
		let url = this.imagepath;
		let img = document.getElementsByClassName('galleryimg' + index);

		// Function to update the images
		this.sliderInterval = setInterval(() => {
			// setInterval(function () {
			$(img).each(function (index) {
				let newImage =
					images[
						(index + Math.floor(Math.random() * images.length)) % images.length
					];
				$(img).attr('src', url + 'project/' + newImage); // Set new image source
			});
		}, 900); // Update every 3 seconds
		// console.log(`Mouse entered image at index: ${index}`);
	}

	private getDismissReason(reason: any): string {
		if (reason === ModalDismissReasons.ESC) {
			return 'by pressing ESC';
		} else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
			return 'by clicking on a backdrop';
		} else {
			return `with: ${reason}`;
		}
	}

	getStripPayemts(packagename, packageamount, paymentcontent) {
		let obj = { name: packagename, amount: packageamount };
		this.paymentData = obj;
		this.modalReference = this.modalService
			.open(paymentcontent, {
				ariaLabelledBy: 'modal-basic-title',
				windowClass: 'myCustomModalClass2',
			})
			.result.then(
				(result) => {
					this.closeResult = `Closed with: ${result}`;
				},
				(reason) => {
					this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
				}
			);
	}

